@include font('bebas',bebas-webfont);

body {
	font-family: Helvetica,sans-serif; }

@mixin bebas {
	font-family: bebas,sans-serif; }

.img-fluid,
.img-fluid-a {
    width: 100%;
    display: block;
    height: auto;
    align-self: center; }

.img-fluid-a {
    width: initial;
    max-width: 100%; }

.page__container {
  max-width: 940px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px; }

a:hover {
  color: $is-active; }
.link_active {
    color: $is-active; }

.h3-heading_bebas {
    font-size: 12px;
    color: $is-active;
    text-transform: uppercase;
    line-height: 1.6;
    margin-bottom: 5px;
    @include bebas; }
.span_mod {
    font-size: 12px;
    line-height: 1.6;
    color: #bababa;
    white-space: nowrap; }
.heading_color {
    color: $is-active;
    font-size: 18px;
    line-height: 1; }
.text_color {
    font-size: 12px;
    line-height: 1.5;
    color: #414141; }
.description__text_more {
    margin-bottom: 50px; }
.h4-heading-line {
    display: flex;
    font-size: 14px;
    line-height: 1;
    text-transform: uppercase;
    color: $is-active;
    margin-bottom: 23px;
    @include bebas;
    &:after {
        content: '';
        flex: 1;
        margin-left: 20px;
        background: url('../img/pattern.png') 0 0; } }
.icons {
    background: $is-active;
    height: 75px;
    width: 75px;
    position: absolute;
    right: 100%;
    margin-right: 10px; }

.icons_slide-1,.icons_slide-2,.icons_video,.icons_blockquote,.icons_link {
    background-repeat: no-repeat;
    background-position: 50% 50%; }

.icons_slide-1 {
      background-image: url('../img/icon-slide-1.png'); }
.icons_slide-2 {
    background-image: url('../img/icon-slide-2.png'); }
.icons_video {
    background-image: url('../img/icon-play.png'); }
.icons_blockquote {
    background-image: url('../img/icon-quotes.png'); }
.icons_link {
    background-image: url('../img/icon-link.png'); }
