html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

nav,
ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

a,
span,
*:before,
*:after {
  display: inline-flex;
  text-decoration: none;
  color: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow {
  width: 0.69em;
  height: 1em;
  fill: #FFF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

@font-face {
  font-family: "bebas";
  src: url("../fonts/bebas-webfont.woff2") format("woff2"), url("../fonts/bebas-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: Helvetica,sans-serif;
}

.img-fluid,
.img-fluid-a {
  width: 100%;
  display: block;
  height: auto;
  align-self: center;
}

.img-fluid-a {
  width: initial;
  max-width: 100%;
}

.page__container {
  max-width: 940px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

a:hover {
  color: #E8603C;
}

.link_active {
  color: #E8603C;
}

.h3-heading_bebas {
  font-size: 12px;
  color: #E8603C;
  text-transform: uppercase;
  line-height: 1.6;
  margin-bottom: 5px;
  font-family: bebas,sans-serif;
}

.span_mod {
  font-size: 12px;
  line-height: 1.6;
  color: #bababa;
  white-space: nowrap;
}

.heading_color {
  color: #E8603C;
  font-size: 18px;
  line-height: 1;
}

.text_color {
  font-size: 12px;
  line-height: 1.5;
  color: #414141;
}

.description__text_more {
  margin-bottom: 50px;
}

.h4-heading-line {
  display: flex;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;
  color: #E8603C;
  margin-bottom: 23px;
  font-family: bebas,sans-serif;
}

.h4-heading-line:after {
