  right: 100%;
  margin-right: 10px;
}

.icons_slide-1, .icons_slide-2, .icons_video, .icons_blockquote, .icons_link {
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.icons_slide-1 {
  background-image: url("../img/icon-slide-1.png");
}

.icons_slide-2 {
  background-image: url("../img/icon-slide-2.png");
}

.icons_video {
  background-image: url("../img/icon-play.png");
}

.icons_blockquote {
  background-image: url("../img/icon-quotes.png");
}

.icons_link {
  background-image: url("../img/icon-link.png");
}

.head {
  display: flex;
  padding: 22px 0;
  align-items: flex-end;
  text-align: left;
  font-family: bebas,sans-serif;
  text-transform: uppercase;
}

@media only screen and (max-width: 620px) {
  .head {
    display: block;
    text-align: center;
  }
}

@media only screen and (max-width: 620px) {
  .head__logo {
    margin-bottom: 20px;
  }
}

.logo {
  color: #E8603C;
  font-size: 36px;
}

.logo__part_lighter {
  color: #f3af9d;
}

.head__nav {
  margin-left: auto;
}

.nav {
  font-size: 16px;
  color: #b9b9b9;
  padding-bottom: 6px;
}

.nav__link {
  margin-left: 24px;
}

.title {
  overflow: hidden;
  margin-bottom: 24px;
}

.title__heading {
  position: relative;
  font-family: bebas,sans-serif;
  text-transform: uppercase;
  background: #E8603C;
  color: #fff;
  font-size: 24px;
  line-height: 1.3;
  padding: 15px 0px 15px 18px;
}

.title__heading:before, .title__heading:after {
  content: "";
  position: absolute;
  height: 61px;
  background: #f3af9d;
  width: 100%;
  top: 0;
}

.title__heading:before {
  right: 100%;
  margin-right: 10px;
}

.title__heading:after {
  left: 100%;
  margin-left: 10px;
}

.blog-post {
  display: flex;
  flex-wrap: wrap;
}

.blog-post__main {
  width: 71%;
  margin-right: 50px;
}

@media only screen and (max-width: 960px) {
  .blog-post__main {
    width: 100%;
    margin-right: 0;
    order: 2;
  }
}

.blog-post__aside {
  flex: 1;
}

@media only screen and (max-width: 960px) {
  .blog-post__aside {
    -ms-flex-order: 1;
  }
}

.post {
  position: relative;
}

.slider {
  position: relative;
  margin-bottom: 24px;
}

.slider__next, .slider__prev {
  position: absolute;
  top: 50%;
  left: 20px;
  cursor: pointer;
  z-index: 50;
  font-size: 54px;
  margin-top: -25px;
}

.slider__next {
  left: auto;
  right: 20px;
  margin-top: -42px;
  transform: rotate(-180deg);
}

.slider-slides__bg {
  position: relative;
  height: 0;
  padding-bottom: 44.8%;
}

.promo-inner {
  display: flex;
  padding-right: 7px;
}

@media only screen and (max-width: 620px) {
  .promo-inner {
    flex-direction: column;
  }
}

.promo-inner__tab {
  min-width: 21%;
  margin-right: 6px;
}

@media only screen and (max-width: 620px) {
  .promo-inner__tab {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
  }
}

.bottom-line {
  background: url("../img/pattern.png");
  height: 14px;
  margin-bottom: 46px;
}

.description__heading {
  font-family: bebas,sans-serif;
  margin-bottom: 20px;
}

.description__text {
  margin-bottom: 22px;
}

.blockquote {
  background: #f5f5f5;
  position: relative;
}

.blockquote-block {
  display: flex;
  padding-top: 27px;
  margin-bottom: 50px;
}

.blockquote-block__text {
  font-size: 13px;
  font-style: italic;
  color: #474747;
}

.blockquote-block__comma {
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 72px;
  transform: rotate(180deg);
  line-height: 1;
  color: #E8603C;
  padding-right: 32px;
  padding-left: 12px;
  margin-bottom: -23px;
}

.link {
  margin-bottom: 50px;
  position: relative;
}

.link__heading {
  font-family: bebas,sans-serif;
  word-spacing: 2px;
  margin-bottom: 10px;
}

.link__bestpsd {
  font-size: 12px;
