  margin-bottom: 50px;
}

.page-pagination {
  font-size: 14px;
  color: #fff;
  background: #f3af9d;
  height: 26px;
  width: 26px;
  padding: 6px 12px 7px 10px;
  margin-right: 4px;
  font-family: bebas,sans-serif;
}

.page-pagination:hover {
